.terminal {
  border-radius: 0 0 0.35rem 0.35rem;
  bottom: 0;
  color: #eee;
  font-family: "Source Code Pro", monospace;
  left: 0;
  line-height: 1rem;
  overflow-y: auto;
  padding: 0.75rem;
  position: absolute;
  right: 0;
  top: 1.75rem;
  white-space: pre-line; }
  .terminal .red {
    color: #d32f2f; }
  .terminal .blue {
    color: #0288d1; }
  .terminal .green {
    color: #9e9d24; }
    .terminal .green.bold {
      color: #cddc39; }
  .terminal .typed-cursor {
    animation: blink 0.75s infinite; }
  .terminal > :last-child {
    display: inline-block;
    margin-bottom: 0.5rem; }
