section.demo {
  background: #1565c0 radial-gradient(circle at 75% 25%, #90caf9, #1565c0) no-repeat;
  color: #fff;
  min-height: 100vh;
  padding: 20vh 16vw;
  z-index: 1; }
  section.demo header {
    display: flex;
    flex-direction: column;
    margin-right: 6vw;
    max-width: 18rem;
    padding: 4vh 0; }
    section.demo header h1,
    section.demo header h2 {
      font-weight: 300;
      margin: 0; }
    section.demo header h1 {
      font-size: 1.5rem; }
    section.demo header h2 {
      font-size: 1rem;
      opacity: 0.65; }
    section.demo header p {
      border-left: 1.5px solid rgba(255, 255, 255, 0.5);
      padding: 0.5rem 1rem;
      margin: 4vh 0; }
    section.demo header .actions {
      align-items: flex-end;
      display: flex;
      flex-grow: 1;
      margin: 2vh 0; }
      section.demo header .actions a {
        align-items: center;
        background-color: rgba(0, 0, 0, 0.25);
        border-radius: 0.2rem;
        color: rgba(255, 255, 255, 0.75);
        display: inline-flex;
        line-height: 2.5rem;
        padding: 0 1.35rem;
        text-decoration: none;
        transition: background-color 0.2s ease-in-out;
        white-space: nowrap; }
        section.demo header .actions a:hover {
          background-color: rgba(0, 0, 0, 0.35); }
        section.demo header .actions a.highlight {
          background-color: rgba(255, 255, 255, 0.25); }
          section.demo header .actions a.highlight:hover {
            background-color: rgba(255, 255, 255, 0.35); }
        section.demo header .actions a + a {
          margin-left: 1rem; }
        section.demo header .actions a .icon {
          font-size: 1rem;
          width: 1.5rem; }

@media (max-width: 1024px) {
  section.demo {
    padding: 10vh 8vw; } }

@media (max-width: 768px) {
  section.demo {
    padding: 4vh 8vw;
    flex-direction: column; }
    section.demo header {
      max-width: unset; }
      section.demo header .actions {
        justify-content: center; } }

@media (max-width: 480px) {
  section.demo header {
    flex-grow: 1;
    margin-right: 0; }
    section.demo header .actions {
      align-items: center;
      flex-direction: column; }
      section.demo header .actions a {
        justify-content: center;
        width: 100%; }
        section.demo header .actions a + a {
          margin: 1rem 0 0; } }
